// navbar background
const addShadow = () => {
	document.addEventListener("DOMContentLoaded", function () {
		const navbar = document.querySelector(".navbar")
		if (window.innerWidth < 992) {
			navbar.classList.add("navbar-shadow")
		}
		window.addEventListener("scroll", function () {
			if (window.innerWidth > 991) {
				if (window.scrollY > 150) {
					navbar.classList.add("navbar-shadow")
				} else {
					navbar.classList.remove("navbar-shadow")
				}
			}
		})
	})
}
addShadow()

// karuzele
const swiper = new Swiper(".swiper", {
	direction: "horizontal",
	loop: true,
	spaceBetween: 20,
	autoplay: {
		delay: 3000, // Opóźnienie między slajdami (w milisekundach)
		disableOnInteraction: false, // Autoplay nie zatrzyma się przy interakcji
	},

	breakpoints: {
		768: {
			slidesPerView: 1.5,
			spaceBetween: 20,
			centeredSlides: true,
		},
		992: {
			slidesPerView: 2,
			spaceBetween: 20,
			centeredSlides: true,
		},
		1200: {
			slidesPerView: 2.6,
			spaceBetween: 20,
			centeredSlides: false,
		},
		1400: {
			slidesPerView: 2.55,
			spaceBetween: 30,
			centeredSlides: false,
		},
		1600: {
			slidesPerView: 3.25,
			spaceBetween: 30,
			centeredSlides: false,
		},
	},

	on: {
		init: function () {
			// Nadanie active-slide do pierwszego aktywnego slajdu przy inicjalizacji
			this.slides[this.activeIndex].classList.add("active-slide")
		},

		slideChange: function () {
			// Usuwanie klasy 'active-slide' ze wszystkich slajdów
			this.slides.forEach((slide) => {
				slide.classList.remove("active-slide")
			})

			// Nadanie 'active-slide' do nowego aktywnego slajdu
			const activeSlide = this.slides[this.activeIndex]
			if (activeSlide) {
				activeSlide.classList.add("active-slide")
			}

			// Aktualizacja paginacji (usunięcie aktywnej kropki z poprzedniej, nadanie nowej)
			this.pagination.update()
		},
	},

	pagination: {
		el: ".swiper-pagination",
		clickable: true,
		type: "bullets", // Zapewnia, że paginacja jest typu 'bullets' i jest aktualizowana
	},

	navigation: {
		nextEl: ".swiper-button-next",
		prevEl: ".swiper-button-prev",
	},
})

