// portfolio section

const portfolio = () => {
	const items = [
		{
			id: 1,
			category: "social",
			title: "Social Media 1",
			image: "./dist/img/KENNYFILMS-min.png",
		},
		{
			id: 2,
			category: "web-design",
			title: "Logo 1",
			image: "./dist/img/12-min.png",
		},
		{
			id: 3,
			category: "social",
			title: "Website 1",
			image: "./dist/img/multi-house-min.png",
		},
		{
			id: 4,
			category: "reklama",
			title: "Social Media 2",
			image: "./dist/img/podomefix-min.png",
		},
		{
			id: 5,
			category: "logo",
			title: "Social Media 5",
			image: "./dist/img/FilmCOMBATJPG-min.png",
		},
		{
			id: 6,
			category: "reklama",
			title: "Social Media 4",
			image: "./dist/img/ZAKRECONY-PODKAST-min.gif",
		},
		{
			id: 7,
			category: "druk",
			title: "Social Media 3",
			image: "./dist/img/Logo-garden-min.png",
		},
		{
			id: 8,
			category: "reklama",
			title: "Social Media 6",
			image: "./dist/img/magnetyczna-pamiatka-min.png",
		},
		{
			id: 9,
			category: "web-design",
			title: "Social Media 7",
			image: "./dist/img/world-around-technology-min.png",
		},
	]

	let loadedIndices = { all: 6 }
	let currentCategory = "all"
	const grid = document.querySelector("#portfolio__items")

	function loadInitialItems() {
		grid.innerHTML = ""
		const startIndex = loadedIndices[currentCategory] || 6
		const filteredItems = items.filter(
			(item) => currentCategory === "all" || item.category === currentCategory
		)
		filteredItems
			.slice(0, startIndex)
			.forEach((item, index) => addItemToGrid(item, index))

		const hasMore = startIndex < filteredItems.length
		document.querySelector(".load_more-button").style.display = hasMore
			? "block"
			: "none"
		// document.getElementById("loadLessBtn").style.display =
		// 	startIndex > 6 ? "block" : "none"
	}

	function loadMoreItems() {
		const filteredItems = items.filter(
			(item) => currentCategory === "all" || item.category === currentCategory
		)
		const startIndex = loadedIndices[currentCategory] || 6
		const nextItems = filteredItems.slice(startIndex, startIndex + 3)
		nextItems.forEach((item, index) => addItemToGrid(item, index + startIndex))

		loadedIndices[currentCategory] = startIndex + nextItems.length

		// if (loadedIndices[currentCategory] >= filteredItems.length) {
		// 	document.querySelector(".load_more-button").style.display = "none"
		// }

		// document.getElementById("loadLessBtn").style.display = "block"
	}
	// function loadLessItems() {
	// 	loadedIndices[currentCategory] = 6
	// 	loadInitialItems()
	// }

	const addItemToGrid = (item, index) => {
		const div = document.createElement("div")
		div.className = "portfolio__items-item"
		div.setAttribute("data-category", item.category)
		div.innerHTML = `
		<img src="${item.image}" alt="${item.title}" loading="lazy">
	`
		grid.appendChild(div)

		let delay = 0
		if (index < 6) {
			delay = index * 0.2
		} else {
			const delayIndex = (index - 6) % 3
			delay = (delayIndex + 1) * 0.1
		}
		div.style.animationDelay = `${delay}s`
		div.classList.add("show")
	}

	const filterItems = (category) => {
		currentCategory = category
		loadedIndices[currentCategory] = 6 // Reset loaded index dla wybranej kategorii
		loadInitialItems()
		setActiveFilterButton(category)
	}

	const setActiveFilterButton = (category) => {
		const buttons = document.querySelectorAll(".filter-btn")
		buttons.forEach((button) => {
			if (button.getAttribute("data-category") === category) {
				button.classList.add("active")
			} else {
				button.classList.remove("active")
			}
		})
	}
	document
		.querySelector(".load_more-button")
		.addEventListener("click", loadMoreItems)
	document.querySelectorAll(".filter-btn").forEach((button) => {
		button.addEventListener("click", () => {
			filterItems(button.getAttribute("data-category"))
		})
	})

	document.getElementById("filter-select").addEventListener("change", (e) => {
		filterItems(e.target.value)
	})

	// Initialize
	loadInitialItems()
}
portfolio()

// burger

const burgerMenu = () => {
	const burger = document.querySelector(".burger")
	const toggleMenu = () => {
		const menu = document.querySelector(".navbar__list")
		const body = document.querySelector("body")
		menu.classList.toggle("menu-active")
		body.classList.toggle("no-scroll")
	}

	const navbarItems = document.querySelectorAll(".navbar__item")
	navbarItems.forEach((link) => {
		link.addEventListener("click", function () {
			const menu = document.querySelector(".navbar__list")
			if (menu.classList.contains("menu-active")) {
				menu.classList.remove("menu-active")
				document.querySelector("body").classList.remove("no-scroll")
			}
		})
	})

	burger.addEventListener("click", toggleMenu)
}
burgerMenu()

const iconCarousel = () => {
	const scrollers = document.querySelectorAll(".scroller")

	// If a user hasn't opted in for recuded motion, then we add the animation
	if (!window.matchMedia("(prefers-reduced-motion: reduce)").matches) {
		addAnimation()
	}

	function addAnimation() {
		scrollers.forEach((scroller) => {
			// add data-animated="true" to every `.scroller` on the page
			scroller.setAttribute("data-animated", true)

			// Make an array from the elements within `.scroller-inner`
			const scrollerInner = scroller.querySelector(".scroller__inner")
			const scrollerContent = Array.from(scrollerInner.children)

			// For each item in the array, clone it
			// add aria-hidden to it
			// add it into the `.scroller-inner`
			scrollerContent.forEach((item) => {
				const duplicatedItem = item.cloneNode(true)
				duplicatedItem.setAttribute("aria-hidden", true)
				scrollerInner.appendChild(duplicatedItem)
			})
		})
	}
}
